import { Suspense } from "react";
import { createRoot } from "react-dom/client";
import { BrowserRouter as Router } from "react-router-dom";
import { ToastContainer } from "react-toastify";

import App from "./app";
import initI18n from "./routes/i18n";
import AuthProvider from "./context/AuthProvider";
import LoadingProvider from "./context/LoadingProvider";
import UserProvider from "./context/UserProvider";
import ErrorBoundary from "./error-boundary";
import LoadingModal from "./modals/Loading";
import ColorThemeProvider from "./context/ColorThemeProvider";
import { LocaleProviderWrapper } from "./context/LocaleProvider";
import { Navbar } from "./components/Navbar";
import PagesList from "./components/PagesList";
import { Wrapper } from "./components/Wrapper";
import { UserDetails } from "./components/UserDetails";
import { ThemeProviderWrapper } from "./components/ThemeProviderWrapper";

import "react-toastify/dist/ReactToastify.css";
import "./styles/main.scss";

// Global initialization
initI18n();

// Sentry.init({
//   dsn: process.env.REACT_APP_SENTRY_DNS,
//   integrations: [new BrowserTracing()],
//   tracesSampleRate: 1.0,
//   tunnel: "/unblocksentry",
// });

const htmlRoot = document.getElementById("root") as HTMLElement;
const reactRoot = createRoot(htmlRoot);

reactRoot.render(
  <ColorThemeProvider>
    <ThemeProviderWrapper>
      <LoadingProvider>
        <LocaleProviderWrapper>
          <AuthProvider>
            <UserProvider>
              <Suspense fallback={<LoadingModal isOpen />}>
                <Wrapper>
                  <ToastContainer
                    draggable
                    closeOnClick
                    pauseOnHover
                    pauseOnFocusLoss
                    theme="light"
                    position="bottom-left"
                    autoClose={3000}
                  />

                  <ErrorBoundary>
                    <Router basename={process.env.PUBLIC_URL}>
                      <Navbar />
                      <UserDetails />
                      <PagesList />
                      <App />
                    </Router>
                  </ErrorBoundary>
                </Wrapper>
              </Suspense>
            </UserProvider>
          </AuthProvider>
        </LocaleProviderWrapper>
      </LoadingProvider>
    </ThemeProviderWrapper>
  </ColorThemeProvider>
);
